.table-container {
  overflow-x: auto;
}

.table {
  width: 100%;
  border-collapse: collapse;
}

.table th,
.table td {
  border: 1px solid #ddd;
  padding: 8px;
  word-wrap: break-word;
  max-width: 150px; /* Adjust this value as needed */
}

.table th {
  background-color: #f2f2f2;
  text-align: left;
}
